<style lang="less" scoped>
  @import './api.less';
</style>

<template>
<div class="bg_ff martop20">

    <Row class="app-resource-group-title">
         <h3>API</h3>
    </Row>
    <Row class="api-content">
        <h2></h2>
        <p><strong>POSTHTTPS</strong></p>
        <p><strong>https://qyapi.weixin.qq.com/cgi-bin/user/update?access_token=ACCESS_TOKEN</strong></p>
        <p><strong></strong></p>
        <Row>
            <pre class="api-code">
            {{appContent}}
            <div style="position: absolute;top: 0px;right: 0px;cursor: pointer;"  class="tag-read"  :data-clipboard-text=apiInfo  @click="copy">
                <Tooltip placement="top" content="" :disabled="disabled">
                    <Icon type="md-document" size='24' />
                </Tooltip>
            </div>
            </pre>
        </Row>
    </Row>

  </div>
</template>

<script>
import { getAppSubjectData } from "@/services/appService.js";
import Clipboard from 'clipboard';

export default {
  name: "appApi",
  components: {},
  props: {
   
  },
  data() {
    return {
        apiInfo:'HHHHHHHHHHHHHHHHHHHH',
        appContent:`{
                "formData": {
                    "order": {
                        "dataSet": [{
                            "transObjCode": "",
                            "tdQty": "",
                            "comment": "",
                            "price": "",
                            "promDeliTime": "",
                            "tdAmount": "",
                            "taxRate": "",
                            "assMeasureUnit": "",
                            "assistQty": "",
                            "taxAmount": "",
                            "assMeasureScale": ""
                        }],
                        "dealerDebit": "",
                        "drDealerLabel": "",
                        "drDealerPaymentTerm": ""
                    },
                    "inPut": {
                        "dataSet": [{
                            
                        }]
                    },
                    "outPut": {
                        "dataSet": [{
                            
                        }]
                    },
                    "handlerUnit": "ID",
                    "handlerRoleName": "",
                    "handler": "ID",
                    "dealerDebitContactPersonName": "",
                    "dealerDebitContactInformation": "",
                    "handlerRole": "ID",
                    "handlerName": "",
                    "biComment": "",
                    "handlerUnitName": "",
                    "drDealerLogisticsTerms": ""
                },
                "listId": "a4897429-f4f2-44a4-ade7-2fe8dc67c3cf",
                "biReferenceId": ""
            } `
    };
  },
  methods: {
      copy() {
        var clipboard = new Clipboard('.tag-read')
        clipboard.on('success', e => {
          console.log('')
          // 
          clipboard.destroy()
        })
        clipboard.on('error', e => {
          // 
          console.log('')
          // 
          clipboard.destroy()
        })
      }
  },
  created() {
  }
};
</script>




